import { Redirect } from "react-router-dom";
import styled from "styled-components";

const Container = styled.div`
  width: 400px;
  height: 290px;
  border: solid 1px black;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px;

  @media (max-width: 500px) {
    width: 80vw;
    height: auto;
  }

`;

const Img = styled.img`
  width: 100%;
  height: 185px;
  cursor: pointer;

  @media (max-width: 550px) {
    height: 34vw;
  }
`;

const Title = styled.h2`

`;

const Link = styled.a`

`;

const P = styled.p`

`;


const BlogComponent = (props) => {
    const { name, tags, fronturl, backurl, imgSrc, link } = props.blog;
    return (
        <Container>
            <div>
                <Img src={imgSrc} onClick={()=>{window.location = link}}></Img>
                <Title>{name}</Title>
            </div>
            <div>
                <hr/>
                <P>{tags}</P>
                {backurl ?
                    <div>
                        <Link href={fronturl}>Reposit贸rio Frontend</Link>
                        <br />
                        <Link href={backurl}>Reposit贸rio Backend</Link>
                    </div> :
                    <div>
                        <Link href={fronturl}>Reposit贸rio Frontend</Link>
                    </div>}
            </div>
        </Container>
    );
};

export default BlogComponent;
